import React, { Component } from "react";
import Image from "./assets/img.jpg";
import "./App.css";

class GetAge extends Component {
  calculate = (date) => {
    const today = new Date().getTime();
    const birth = new Date(date).getTime();
    const diff = Math.abs(today - birth);
    let days = Math.floor(diff / (1000 * 3600 * 24));
    let years = Math.floor(days / 365);
    days -= years * 365;
    let month = Math.floor(days / 31);
    days -= month * 31;
    return `${years} years ${month} months ${days} days`;
  };

  render() {
    return (
      <div className="fade">
        <h1>{this.props.date}</h1>
        <h1 className="birthString">{this.calculate(this.props.date)}</h1>
        <img src={Image} alt="" className="img" />
      </div>
    );
  }
}

export default GetAge;
