import React, { Component } from "react";
import GetAge from "./GetAge";
import "./App.css";

class Age extends Component {
  state = {
    birthDate: "",
    getAge: false,
  };
  ageCalculate = () => {
    const date = document.getElementById("birthdate").value;
    if (date !== "") {
      this.setState({ birthDate: date });
      this.setState({ getAge: true });
    } else {
      this.setState({ getAge: false });
    }
  };
  render() {
    return (
      <div className="center">
        <h1 className="header">Age Calculator</h1>
        <span className="blue">Enter Your Birth Date : </span>
        <input type="date" id="birthdate" className="datepicker" />
        <button onClick={this.ageCalculate} className="btn">
          Calculate Age
        </button>
        {this.state.getAge ? (
          <GetAge date={this.state.birthDate} />
        ) : (
          <h1>Please Select Date</h1>
        )}
        <footer>All Right Reserved</footer>
      </div>
    );
  }
}

export default Age;
